import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { makeStyles, Grid } from "@material-ui/core";
import { configRequest } from "../../requests";

//Components
import {
  AppBar,
  Tabs,
  Tab,
  Box,
  Checkbox,
  FormControlLabel,
} from "@material-ui/core/";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    borderRadius: "8px",
    padding: "5px",
    width: "95%",
    margin: "auto",
  },
  appBar: {
    border: "none",
    boxShadow: "none",
    backgroundColor: "transparent",
    color: "#555555",
  },
  tab: {
    overflowY: "auto",
    textAlign: "left",
    marginTop: "15px",
    "& .MuiBox-root": {
      padding: "0px",
    },
    "& .MuiCheckbox-root": {
      margin: "0",
      padding: "0px",
    },
    "& .MuiFormControlLabel-root": {
      margin: "0",
    },
  },
}));

const isTabActiveStyle = (configArr, filter) => {
  const active = configArr.filter((el) => filter.includes(el.value)).length > 0;
  return active ? { color: "crimson", fontWeight: "bold" } : {};
};

export default function WorkoutFilterConfig({ filter, setFilter, tab }) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [config, setConfig] = useState({});

  const openValue = (tab) => {
    if (config.Style.some((el) => el.value === tab)) setValue(0);
    if (config.Duration.some((el) => el.value === tab)) setValue(1);
    if (config.Equipment.some((el) => el.value === tab)) setValue(2);
  };

  useEffect(() => {
    configRequest()("", (res) => {
      const { style, duration, equipment } = res.data;
      setConfig({
        Style: style,
        Duration: duration,
        Equipment: equipment,
      });
    });
  }, []);

  useEffect(() => {
    if(Object.keys(config).length > 0) {
      openValue(tab);
    }
  }, [tab,config]);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" className={classes.appBar}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="simple tabs example"
          variant="scrollable"
          scrollButtons="auto"
        >
          {Object.keys(config).map((key, idx) => (
            <Tab
              key={idx}
              label={key}
              style={isTabActiveStyle(config[key], filter)}
              disableRipple
              {...a11yProps(idx)}
            />
          ))}
        </Tabs>
      </AppBar>
      {Object.values(config).map((checkboxes, idx) => (
        <TabPanel key={idx} value={value} index={idx} className={classes.tab}>
          <Grid container>
            {checkboxes.map((el,i) => (
              <Grid item xs={6} key={i}>
                <FormControlLabel
                  value={el.value}
                  control={<Checkbox checked={filter.includes(el.value)} />}
                  label={el.name}
                  onChange={(e) => setFilter(e.target.value)}
                />
              </Grid>
            ))}
          </Grid>
        </TabPanel>
      ))}
    </div>
  );
}
