import React, { useEffect, useState } from "react";
import { workoutRequest } from "../../requests";

//Components
import WorkoutFilterConfig from "../filterworkouts/filterWorkouts";
import {
  Grid,
  Paper,
  makeStyles,
  IconButton,
  Badge,
  Chip,
  Button,
} from "@material-ui/core";

//Icons
import { Autorenew, FilterList, Clear } from "@material-ui/icons";

const useStyles = makeStyles(() => ({
  root: {
    padding: "10px",
    textAlign: "center",
  },
  content: {
    color: "#30475e",
    fontSize: "1.1rem",
    fontWeight: "bold",
    minHeight: "180px",
    marginTop: "15px",
  },
  filterBtn: {
    textAlign: 'left'
  },
  loadBtn: {
    textAlign: "right",
  },
  closeBtn: {
    marginTop: "20px",
    fontWeight: "bold",
  },
  filterChips: {
    marginTop: "30px",
    display: "flex",
    flexWrap: "wrap",
  },
  chip: {
    margin: "5px",
    minWidth: "60px",
    maxWidth: "fit-content",
    display: "flex",
    justifyContent: "space-between",
  }
}));

export default function RandomWorkout() {
  const classes = useStyles();
  const [workout, setWorkout] = useState([]);
  const [filter, setFilter] = useState([]);
  const [showFilter, toggleFilter] = useState("amrap");

  const handleCLick = () => {
    setFilter([...filter]);
  };

  const handleShowFilter = (tab) => () => {
    if (showFilter) {
      toggleFilter("");
    } else {
      toggleFilter(tab);
    }
  };

  const handleFilter = (value) => {
    if (filter.includes(value)) {
      setFilter(filter.filter((el) => el !== value));
    } else {
      setFilter([...filter, value]);
    }
  };

  const handleDelete = (todelete) => () => {
    handleFilter(todelete);
  };

  useEffect(() => {
    if (filter.length > 0) {
      const queryString = filter
        .map((el) => `filter[]=${el}`)
        .toString()
        .replaceAll(",", "&");

      workoutRequest()(queryString, (res) => {
        if (res.status === "success") {
          setWorkout(res.data.split("\n"));
        } else {
          setWorkout([res.msg]);
        }
      });
    }

    if (filter.length === 0 && workout.length > 0) {
      toggleFilter("");
    }
  }, [filter]);

  return (
    <Paper elevation={4} className={classes.root}>
      <Grid container>
        <Grid item xs={6} className={classes.filterBtn}>
          <IconButton
            color={filter.length > 0 ? "secondary" : "primary"}
            onClick={handleShowFilter("amrap")}
          >
            <Badge badgeContent={filter.length} color="secondary">
              <FilterList />
            </Badge>
          </IconButton>
        </Grid>
        <Grid item xs={6} className={classes.loadBtn}>
          {!showFilter && filter.length > 0 && (
            <IconButton onClick={handleCLick} color="secondary">
              <Autorenew />
            </IconButton>
          )}
        </Grid>
        <Grid item xs={12} className={classes.content}>
          {showFilter ? (
            <div>
              <WorkoutFilterConfig
                filter={filter}
                setFilter={handleFilter}
                tab={showFilter}
              />
              <Button
                onClick={() => toggleFilter("")}
                className={classes.closeBtn}
              >
                {workout.length > 0 ? "show Workout" : "close"}
              </Button>
            </div>
          ) : (
            workout.map((el, i) => <div key={i}>{el}</div>)
          )}
        </Grid>
        <Grid item xs={12} className={classes.filterChips}>
          {filter.length > 0 && (
            <IconButton onClick={() => setFilter([])} style={{padding: '5px'}}>
              <Clear />
            </IconButton>
          )}
          {filter.map((el, i) => (
            <Chip
              key={i}
              variant="outlined"
              color="secondary"
              size="small"
              label={el}
              onDelete={handleDelete(el)}
              onClick={() => toggleFilter(el)}
              className={classes.chip}
            />
          ))}
        </Grid>
      </Grid>
    </Paper>
  );
}
